<!-- 集市下面的油脂油料组件 -->
<template>
	<div id="YouZhiYouLiao">
        <v-headr :myname="myname"></v-headr>
        <section class="aui-scrollView YouZhiYouLiao">  
            <div class="aui-card-list"> 
                <div v-show="sosuo">



                    <div class="area-title"><h4><!-- <span class="more" @click="goMore('0564')">更多dd</span> -->热门品种</h4></div>
                    <ul class="yy-mod">
                        <div style="display:none;"> {{indexCount = 0}}</div>
                        <li v-for="item,index in youzhi_hot" v-if="item.cropCategoryName.length == 4 && indexCount < 6 " class="main_cat" @click="goSch(item)">{{item.cropCategoryName}} <span style="display:none">{{indexCount++}}</span>  </li>

                        <!-- <li @click="goToSerch('0550','0')" class="main_cat" :class="{'active':isA == 0}">国产油用大豆</li> 
                        <li @click="goToSerch('0564','1')" class="main_cat" :class="{'active':isA == 1}">棉籽</li>
                        <li @click="goToSerch('0359','2')" class="main_cat" :class="{'active':isA == 2}">二级芝麻油</li>
                        <li @click="goToSerch('0352','3')" class="main_cat" :class="{'active':isA == 3}">菜籽油</li>
                        <li @click="goToSerch('0360','4')" class="main_cat" :class="{'active':isA == 4}">精炼茶籽油</li>
                        <li @click="goToSerch('0333','5')" class="main_cat" :class="{'active':isA == 5}">进口豆油</li>  -->
                    </ul>

                    <div class="area-title"><h4><!-- <span class="more" @click="goMore('0435')">更多yl</span> -->油厂报价</h4></div>
                    <ul class="yy-mod">
                        <div style="display:none;"> {{indexCount2 = 0}}</div>
                         <li v-for="item,index in youzhi_hot" v-if="item.cropCategoryName.indexOf('油') > -1 && indexCount2 < 6 " class="main_cat" @click="goSch(item)">{{item.cropCategoryName}} <span style="display:none">{{indexCount2++}}</span>  </li>

                        
                        <!-- <li @click="goToSerch('0329','6')" class="main_cat" :class="{'active':isA == 6}">24度棕榈油</li> 
                        <li @click="goToSerch('0353','7')" class="main_cat" :class="{'active':isA == 7}">一级菜籽油</li>
                        <li @click="goToSerch('0362','8')" class="main_cat" :class="{'active':isA == 8}">一级花生油</li>
                        <li @click="goToSerch('0327','9')" class="main_cat" :class="{'active':isA == 9}">四级豆油</li> -->
                        
                    </ul>

                </div>

                <!-- <div class="area-title"><h4><span class="more">更多</span>市场分析</h4></div> -->
                <div > <!-- v-show="liebiao" -->
                 <a href="javascript:void(0);"
                 class="aui-panel-cell"
                 v-for="(item,index) in list" > 

                 <div class="aui-panel-cell-bd">
                    <h4>{{item.title}}</h4>
                    <span>{{item.date}}</span>
                </div> 
                <div class="float-rigt">
                    <em class="jiage">{{item.price}}元</em>
                </div>
            </a>
        </div>

    </div>


</section>
</div>
</template>

<script type="text/javascript">
import Headr from '../../components/header.vue'
export default {
  name:'YouZhiYouLiao',
  data(){
     return {
        msg:'集市下面的油脂油料组件',
        myname:{
            name:'油脂油料',
            IsSubPage:true
        },
        list:null,
        sosuo:true,
        liebiao:false,
        isA:null,
        youzhi_hot:null
    }
},
components:{
    'v-headr':Headr,
},
methods:{
    getUrlParam(key) {
      // 获取参数
      var url = window.location.search;
                // 正则筛选地址栏
                var reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)");
                // 匹配目标参数
                var result = url.substr(1).match(reg);
                //返回参数值
                return result ? decodeURIComponent(result[2]) : null;
            },
            childClick(){
                this.$emit('childByValue',this.IsSubPage)
            },
            goSch(items){
                console.log(items);
                window.location.href="/JiShi/ZongHeChaXun?hotCityID="+items.cropCategoryCode;
            },
            init_list(){
                console.log("初始化列表"); 
                var isSear = this.getUrlParam("isSearch");
                var lisCity = this.getUrlParam("Cty");

                if(isSear!=null){
                    this.liebiao = true;
                    this.sosuo = false;

                    var tmpArr = new Array();
                    var list_1 = dataList_YoZhi.result[0].JinKouDaDou;
                    var list_2 = dataList_YoZhi.result[0].YouYongDaDou;
                    var list_3 = dataList_YoZhi.result[0].ShiYongDaDou;
                    for(let i in list_1){tmpArr.push(list_1[i])}
                        for(let j in list_2){tmpArr.push(list_2[j])}
                            for(let k in list_3){tmpArr.push(list_3[k])}   
                                this.getliebiao(tmpArr);

                        }

                    },
                    goMore(){

                    },
                    goCity(key){
                        this.$router.push({ name: 'YouZhiYouLiaoCont', query: { q: key }})
                    },
                    getliebiao(list_1){
                        console.log(list_1)
                        this.list= list_1; 
                    },
                    goToSerch(kind,index){
                        this.isA = index; 
                        window.location.href="/JiShi/ZongHeChaXun?hotCityID="+kind;
                    },
                    goMore:function(q){  
                 // this.$router.push('/SanDaZhuLiang_Cont');
                 this.$router.push({ name: 'SanDaZhuLiang_Cont', query: { q: q }})
             },
             getdata:function(url,params={}){
                return new Promise((resolve,reject) => {
                  this.axios.get(url,{
                    params:params
                })
                  .then(response => {
                    resolve(response.data);
                })
                  .catch(err => {
                    reject(err)
                })
              })

            }

        },
        mounted(){

            // http://hx.mac/api/flxoa/CropCategory
            this.getdata('/api/flxoa/CropCategory').then((response) => {
                this.youzhi_hot = response;
          })  

            this.childClick();
            this.init_list();

        }
    }
    </script>

    <style>
    .yy-mod li.main_cat { overflow:hidden;}
    </style>


