<!-- 集市下面的营养饲料组件 -->
<template>
    <div id="YingYangSiLiao">
         <v-headr :myname="myname"></v-headr>
        <section class="aui-scrollView YingYangSiLiao">  
                <div class="aui-card-list"> 
                    <div class="area-title"><h4><!-- <span class="more"  @click="goMore('0101')">更多</span> -->热门品种</h4></div>
                    <ul class="yy-mod">
                        <div style="display:none;"> {{indexCount3 = 0}}</div>
                         <li v-for="item,index in yingyangHot" v-if="item.parentId = '01' && indexCount3 < 6 " class="main_cat" @click="goSch(item)">{{item.cropCategoryName}} <span style="display:none">{{indexCount3++}}</span>  </li>
                    </ul>

                    <div class="area-title"><h4><!-- <span class="more" @click="goMore('0118')">更多</span> -->最新价格</h4></div>

                   <a href="javascript:void(0);" class="aui-panel-cell" v-for="(item,key) in dataList" 
                   @click="goInfo(item)"
                   >
                        <div class="aui-panel-cell-bd">
                            <h4>{{item.HxCropofferStr}}</h4>
                            <span>{{item.OfferDate}}</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage" >{{item.Price}}</em>
                            <span class="jiantou" :style="{backgroundImage:jiantou}" :class="{active: item.todayTrend < 0}"><i style="display: none;">{{item.todayTrend}}</i></span>

                        </div>
                    </a>
                </div>
                

            </section>
    </div>
</template>

<script type="text/javascript">
    import Headr from '../../components/header.vue'
    export default {
        name:'YingYangSiLiao',
        data(){
            return {
                msg:'集市下面的优质饲料组件',
                myname:{
                    name:'营养饲料',
                    IsSubPage:true
                },
                jiantou:'url(' + require('../../assets/img/arrowbig.png') + ')',
                dataList:[],
                up:'',
                dow:'',
                isA:null,
                yingyangHot:null
            }
        },
        components:{
            'v-headr':Headr,
        },
        methods:{
            goToSerch(kind,index){
                this.isA = index;
                window.location.href="/JiShi/ZongHeChaXun?hotCityID="+kind;
            },
            chilClick(){
                this.$emit('childByValue',this.IsSubPage)
            },
            /*获取数据*/
            getListData(){
                this.$indicator.open();
                this.axios.get('/api/flxoa/CropOffer',{
                    params:{
                       CropCategoryCode:'0150', 
                       iscropcategory:1,
                    }
                }).then(res => {
                    //console.log("营养饲料列表部分成功"+res)
                    //console.log("营养饲料列表部分成功"+JSON.stringify(res) )
                    this.$indicator.close();
                    this.dataList = res.data[0].data

                    console.log(res.data[0].data)
                    //console.log(JSON.stringify(res))
                    console.log(this.dataList)
                    
                },err => {
                     console.log("营养饲料列表部分失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })

            },
            goMore:function(q){  
                 // this.$router.push('/SanDaZhuLiang_Cont');
                 this.$router.push({ name: 'SanDaZhuLiang_Cont', query: { q: q }})
             },
             goInfo:function(keys){
              this.$router.push({ name: 'SanDaZhuLiangXQ', query: { Region: keys.RegionCode,CropCategory: keys.CropCategoryCode,Price:keys.Price,CropOfferStr:keys.HxCropofferStr}})
            },
            goSch(items){
                console.log(items);
                window.location.href="/JiShi/ZongHeChaXun?hotCityID="+items.cropCategoryCode;
            },
            getdata:function(url,params={}){
                return new Promise((resolve,reject) => {
                  this.axios.get(url,{
                    params:params
                })
                  .then(response => {
                    resolve(response.data);
                })
                  .catch(err => {
                    reject(err)
                })
              })

            }


        },
        mounted(){
            this.chilClick()
            this.getListData()
            // http://hx.mac/api/flxoa/CropCategory
            this.getdata('/api/flxoa/CropCategory').then((response) => {
                this.yingyangHot = response;
          })  

            //this.dataList = YingYangSiL.result[0].ShengZhuHuoJi
        }
    }
</script>
<style type="text/css" scoped>
    .jiageD{
        color: #279637;
        font-size: 16px;
       /* display: none;*/
    }
     .jiantou{
        width: 12px;
        height: 22px;
        display: inline-block;
        background-repeat: no-repeat;
        background-position: 0px -15px;
        background-size: 64%;
    }
    .jiantou.active{
        width: 12px;
        height: 22px;
        display: inline-block;
        background-repeat: no-repeat;
        background-position: 0px 9px;
        background-size: 64%;
    }
    .yy-mod li.main_cat { overflow:hidden;}
</style>


